import { CART_ADD_ITEM, CART_REMOVE_ITEM,CART_SAVE_SHIPPING_ADDRESS,CART_SAVE_PAYMENT_METHOD,CART_CLEAR_ITEMS } from "../constants/cartConstants";
export const CartReducer = (state = { cartItems: [] ,ShippingAddress:{} , PaymentMethod:{}}, action) => {
    switch (action.type) {
        case CART_ADD_ITEM:
            const item = action.payload
            // check if item exist in cartItems so we loop in objects if exist update it if not  add action payload to cartitems array
            const existItem = state.cartItems.find(x => x.product === item.product)
            if (existItem) {
                return {
                    ...state,
                    cartItems: state.cartItems.map(x =>
                        x.product === existItem.product ? item : x)
                }

            } else {
                return {
                    ...state,
                    cartItems: [...state.cartItems, item]
                    

                }}
        case CART_REMOVE_ITEM:
                return{
                    ...state,
                    cartItems:state.cartItems.filter(x => x.product !== action.payload)

                }
        case CART_SAVE_SHIPPING_ADDRESS:
            return{
                ...state,
                ShippingAddress:action.payload
            }
        case CART_SAVE_PAYMENT_METHOD:
            return{
                ...state,
                PaymentMethod:action.payload
            }
        case CART_CLEAR_ITEMS:
            return{
                ...state,
                cartItems:[]
            }
        default:
                return state;
            }
};


